<template>
  <div class="container">
    <el-row>
      <el-col :span="8">
        <h1 class="title">{{pageName}}采购单明细</h1>
        <label for="">SKU</label><el-input v-model="task.sku" placeholder="请输入产品SKU"></el-input>
        <label for="">平台单号</label><el-input v-model="task.supplier_ref" placeholder="请输入平台单号"></el-input>
        <label for="">采购链接</label><el-input v-model="task.link" placeholder="请输入产品采购链接"></el-input>
        <label for="">采购价格</label><el-input v-model="task.price" placeholder="请输入采购价格"></el-input>
        <label for="">采购数量</label><el-input v-model="task.count" placeholder="请输入采购数量"></el-input>

        <label for="">状态</label>
        <el-select v-model="task.status" placeholder="请选择">
          <el-option
                  v-for="status in typeOptions"
                  :key="status.value"
                  :label="status.label"
                  :value="status.value">
          </el-option>
        </el-select>

        <!--<label for="">供应商名称</label><el-input v-model="task.supplier_name" placeholder="请输入供应商名称"></el-input>-->
        <label for="">备注</label><el-input v-model="task.memo" placeholder="备注"></el-input>

      </el-col>
    </el-row>
    <div class="footerButton border-shadow">
      <el-button type="primary" @click="submitTask()" style="vertical-align: top">提交</el-button>
    </div>
  </div>
</template>

<script>
    export default {
        name: "addTask",
        data() {
            return {
                pageName: '新增',
                task: {
                    purchase_id: '',
                    sku: '',
                    supplier_ref: '',
                    link: '',
                    price: '',
                    count: '',
                    status: '',
                    memo: ''
                },

                typeOptions: [
                    // {
                    //     label: '待采购',
                    //     value: '0'
                    // },
                    {
                        label: '已采购',
                        value: '1'
                    },
                    // {
                    //     label: '缺货',
                    //     value: '2'
                    // },
                    // {
                    //     label: '取消单',
                    //     value: '3'
                    // },
                ]
            }
        },
        methods: {
            submitTask() {
                let url = "http://api.orkowms.me/purchases-detail";
                let params = {};
                params.purchase_id = this.task.purchase_id;
                params.sku = this.task.sku;
                params.supplier_ref = this.task.supplier_ref;
                params.link = this.task.link;
                params.price = this.task.price;
                params.count = this.task.count;
                params.status = this.task.status;
                params.memo = this.task.memo;

                let that = this;
                this.$axios.post(url,params).then((res) => {
                    // document.write(res);exit;

                    that.$message({
                        type: 'success',
                        message: '新增成功!'
                    });

                }).catch((err) => {

                    that.$message({
                        type: 'error',
                        message: '新增失败!'
                    });
                })
            },
        },
        created() {
            let row = this.$route.query;
            this.task.purchase_id = row.id;
            // this.task.supplier_ref = row.supplier_ref;
            // this.task.price = row.price;
            // this.task.count = row.count;
            // this.task.status = row.status;
            // this.task.memo = row.memo;
        }
    }
</script>

<style scoped lang="stylus">
  .container
    .el-row
      margin 30px 30px 0px 30px
    label
      display block
      font-weight bold
      font-size: 20px
      margin: 10px 0
    .title
      margin-bottom: 50px
    .button
      margin-top: 20px
    .upload-img
      display inline-block
      margin-right 30px
    .footerButton
      position absolute
      left: 0
      bottom 0
      width: 100%
      padding 13px 30px
      box-sizing border-box
      background-color: hsla(0,0%,100%,.9);
      z-index 2
      button
        float: right;

</style>



